  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 17px 20px;
  color: #484848;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}
.custom-select__item:hover {
  color: #316dc2;
}
.custom-select__item:focus {
  outline: none;
}
.custom-select__item:active {
  color: #ffffff;
  background-color: #316dc2;
}
.custom-select__item:focus-visible {
  color: #ffffff;
  background-color: #316dc2;
  outline: none;
}
.custom-select__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  color: #f0efe9;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #ffffff;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.is-disabled {
  pointer-events: none;
}
.custom-select.is-disabled .custom-select__list {
  display: none;
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  background-color: rgba(255, 94, 102, 0.2);
  border: 1px solid #ff5e66;
}
.custom-select__label--modal {
  color: rgba(72, 72, 72, 0.8);
}
.custom-select__button--modal {
  border: 1px solid rgba(72, 72, 72, 0.3);
}
.custom-select__button--modal:hover {
  border: 1px solid #484848;
}
.custom-select__button--modal:focus-visible {
  border: 1px solid #484848;
  outline: none;
}
.custom-select__button--modal:focus {
  border: 1px solid #484848;
  outline: none;
}
.custom-select__button--modal:active {
  border: 1px solid #484848;
}
.custom-select__button--modal:disabled {
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
}
.custom-select__button--modal:hover ~ .custom-select__label, .custom-select__button--modal:active ~ .custom-select__label, .custom-select__button--modal:focus ~ .custom-select__label {
  color: #484848;
}
.custom-select__list--modal {
  border: 1px solid #484848;
}
.custom-select__text--modal {
  color: #484848;
}
.custom-select__icon--modal {
  background-color: #484848;
}

